import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import axios from "axios";
import LoadingOverlay from 'react-loading-overlay'
import MUIDataTable from "mui-datatables";
import { Grid, Card, MuiThemeProvider, TablePagination } from "@material-ui/core";
import routes from "../../routes/routes";
import { useSelector } from "react-redux";
import moment from 'moment'
import { Menu, MenuItem, MenuButton } from "@szhsin/react-menu";
import Button from '@material-ui/core/Button';
import Pdf from "react-to-pdf";

const ref = React.createRef();
const Pdfoptions = {
  orientation: 'landscape',
};



const OrdersList = (props) => {
  const [page, setPage] = useState(0);
  const [totalCount, setTotalCount] = useState(0);
  const [isActive, setIsActive] = useState(false);
  const [warehouses, setWarehouses] = useState([]);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [purchaseOrder, setPurchaseOrder] = useState([]);
  const [isAdd, setIsAdd] = React.useState(false);
  const [anchorEl, setAnchorEl] = React.useState(null);

  const { userToken } = useSelector((state) => state.Auth);
  const history = useHistory();

  const handleChangePage = (event, newPage) => {
    console.log("newPage", newPage);
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    console.log("parseInt", parseInt(event.target.value, 10));
    setRowsPerPage(parseInt(event.target.value, 10));
    // setPage(0);
  };
  const CreateOrderHandleChange = () => {
    history.push(`/dashbord/createNewOrder`);
  }
  const handleProduct = (rowData) => {
    history.push("/dashbord/orderReceive", rowData);
  };
  
  const handleCloseMenuButton = () => {
    setAnchorEl(null);
  };
  const handleClickMenuButton = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const getAllPurchaseOrder = async () => {
    let purchaseOrderData = [];

    // setIsActive(true);
    // setRefresh(true);
    const pData = axios({
      method: "get",
      url: routes.api.POorders.getAllPurchaseOrders.url,
      headers: { Authorization: "Bearer " + userToken },
    });
    const [PO_order] = await axios.all([pData]);
    console.log("PO_order", PO_order.data)
    
    PO_order.data.doc.map((data, index) => {
      let newdata = {
        _id:data._id,
        quantity:0,
        vendor: data.vendor_id.name,
        orderNumber:data.orderNumber,
        discount:data.discount,
        fee:data.fee,
        status:data.status,
        // productName:item.productName,
        expectedDate:moment(data.expectedDate).format('L'),
        subTotal:data.fee - data.discount  ,
        received:'-',
        paid:'-',
        location:"United State"
      };
      data.products && data.products.forEach((item, index) => {
          newdata.quantity=newdata.quantity+parseInt(item.quantity)
        })
        console.log("newdata", newdata)
        
        purchaseOrderData = [...purchaseOrderData, newdata];
        
        // if(purchaseOrderData.orderNumber)
        return purchaseOrderData;
      
    });
    console.log("purchaseOrderData", purchaseOrderData)

    setPurchaseOrder(purchaseOrderData);
    // setIsActive(false);
  };

  const columns = [
    {
      name: "_id",
      label: "id",
      options: {
        display: false,
      },
    },
        
    {
      name: "status",
      label: "Status",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },

    {
      name: "orderNumber",
      label: "Order #",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue, rowIndex) => (
          <span
            onClick={() => {
              handleProduct(purchaseOrder[tableMeta.rowIndex]);
            }}
            className="linkSpan"
          >
            {value}{" "}
          </span>
        ),
      },
    },
    {
      name: "location",
      label: "Ship To",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "vendor",
      label: "Vendor",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },

    {
      name: "quantity",
      label: "Quantity",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "fee",
      label: "Cost",
      options: {
        display:false,
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "discount",
      label: "Discount",
      options: {
        display:false,
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "tax",
      label: "Tax",
      options: {
        display:false,
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },

    
    {
      name: "subTotal",
      label: "SubTotal",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },

    
    {
      name: "expectedDate",
      label: "Due",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "received",
      label: "Received",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "paid",
      label: "Paid",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },


  ];

  const option = {
    filterType: "dropdown",
    selectableRowsHideCheckboxes: true,
    pagination: false,
    search:false,
    sort:false,
    pagination:false
  };

  let productData = [];

 

  useEffect(() => {
    getAllPurchaseOrder();
    

  }, []);
  // console.log("products", products);

  return (
    <LoadingOverlay
      active={isActive}
      spinner
      text='Loading ...'
    >
    <div>
    {/* <header className="headerOrder">
        <div className="header__row">
          <div className ="Texts_Display">
             Purchase Orders
          </div>
          <div>
            <Button
              aria-controls="simple-menu"
              aria-haspopup="true"
              variant="contained"
              color="primary"
              className="buttonColor"
              onClick={handleClickMenuButton}
            >
              Action
            </Button>
            
            <Menu menuButton={<Button>Action</Button>}

            >

            < Pdf targetRef={ref} filename="order.pdf" options={Pdfoptions}>
                {({ toPdf }) =>  
                 <MenuItem style={{color:'#8CC5EB' , borderBottom:'1px solid gray'}}
                    onClick={toPdf}
                    // disabled
                    >

                    Download PDF
                </MenuItem>}
              </Pdf>

            </Menu>

            <Button
              variant="contained"
              color="primary"
              className="buttonColor ml-2"
              onClick={CreateOrderHandleChange}
            >
              Create PO
            </Button>
          </div>

        </div>
      </header> */}
      <div>
        <Grid item xs={12} sm={12}>
          <Card>
            <MuiThemeProvider>
              <MUIDataTable
                style={{ th: { backgroundColor: "grey" } }}
                //   title="Employees List"
                data={purchaseOrder}
                columns={columns}
                options={option}
              />
              <TablePagination
                component="div"
                count={totalCount}
                page={page}
                onPageChange={handleChangePage}
                rowsPerPage={rowsPerPage}
                onRowsPerPageChange={handleChangeRowsPerPage}
              />
            </MuiThemeProvider>
          </Card>
        </Grid>
      </div>
    </div>
    </LoadingOverlay>
  );
};
export default OrdersList;
